$(function () {
  var buttSearch = $(".buttSearch").children("span");
  var closeMenu = $(".closeMenu");
  var menuOpen = $(".menuOpen");
  var menuCabinet = $(".menuCabinet");
  var menuCenter = $(".menuCenter");
  var submenuItem = $(".submenuItem").children("span");
  var submenu = $(".submenu");
  var titleSubmenu = $("li.titleSubmenu");
  var burger = $("#burger");
  var menuItem = $(".menuItem").children("span");
  var accordion = $('.titleItem');

  $(accordion).click(function (a) {
    $(this).parent().toggleClass('active');
  });

  $(buttSearch).click(function (a) {
    funMenuOpen(a);
  });
  $(closeMenu).click(function (a) {
    $(menuOpen).css("display", "none");
  });

  function funMenuOpen(a) {
    $(menuOpen).css("display", "none");
    $(a.target)
      .next(".menuOpen")
      .css("display", "block");
  }

  if (screen.width < 768) {
    $(submenuItem).click(function (a) {
      burgerMenuSecondLvl(a);
    });
    $(menuItem).click(function (a) {
      burgerMenu(a);
    });
    $(titleSubmenu).click(function (a) {
      closeMenuFunc(a);
    });
    $(burger).click(function () {
      $(this).toggleClass('burgerOpen');
      if (parseInt($(".menuCenter").css("left")) < 0) {

        $(".menuCenter").css("left", "0");
        // $(".langOpen").css("left", "0");
      } else {
        $(".menuCenter").css("left", "-360px");
        $(submenu).css("left", "-360px");
        $(".submeItemBloc").css("left", "-360px");
        // $(".langOpen").css("left", "-260px");
      }
    });
  }
  function burgerMenu(e) {
    e = e.target;
    var titleText = e.innerText;
    $(e)
      .next()
      .css("left", "0px");
    $(e)
      .parent()
      .children(submenu)
      .children("li.titleSubmenu")
      .text(titleText);
  }
  function closeMenuFunc(e) {
    $(e.target)
      .parent()
      .css("left", "-360px");
  }
  function burgerMenuSecondLvl(e) {
    e = e.target;
    var titleText = e.innerText;
    $(e)
      .next()
      .css("left", "0px");
    $(e)
      .parent()
      .children()
      .children("li.titleSubmenu")
      .text(titleText);
  }
  $(document).mouseup(function (e) {
    if (
      !menuOpen.is(e.target) &&
      !menuCabinet.is(e.target) &&
      menuCabinet.has(e.target).length === 0
    ) {
      $(menuOpen).css("display", "none");
    }
  });
});